
.card{
  max-width: 300px;
  border: 1px solid black ;
  border-radius: 10px;
  margin-top: 10px;
  
  
}
.card img{
  width: 100%;
  overflow: hidden;
  
}
.card h3 .card p {
  margin: 6px 0;
}
.card-content{
  padding: 0 6px;
}
.container{
  display: flex;
  flex-wrap: wrap;
  gap:12px;
}